import { HJson, MyUI } from "@macashipo/mlib"
import React from "react"
import {
  FlowGetConfigOfConfigPage,
  FlowGetConfigMyUI,
} from "src/helpers/Flows/ConfigPage"
export default function WithMyUI(props) {
  let _configOfConfigPage = FlowGetConfigOfConfigPage(props.configPage)
  let _configMyUI = FlowGetConfigMyUI(props.configPage)
  return (
    <MyUI
      type={_configMyUI.type || "html"}
      configPage={props.configPage}
      {..._configMyUI}
    />
  )
}
